import React, {
  useCallback, useEffect, useMemo, useRef, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack, Input, Center, ListItem, List, WrapItem, Wrap, Flex, Heading
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import InfiniteScroll from 'react-infinite-scroll-component';
import { useNavigate } from 'react-router-dom';
import Icon from '../../../components/Icon';
import style from './ContactList.module.css';
// import { useModulePageData } from '../../../hooks/module';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { getAllContacts } from '../../../store/middlewares/contact';
import { contact } from '../../../types/contact';
import { updateSelectedContact } from '../../../store/actionCreators/contact';
import { useDebouncedEffect } from '../../../hooks/app';
import { sendMessage } from '../../../utils/sdk';
import SkeletonLoader from '../../../components/SkeletonLoader';
// import MiniBox from '../../../layout/MiniBox';
// import { ContactListData } from '../../../modules/Contacts/ContactList';
// import { Data } from '../../../modules/Contacts';
import { COMMON } from '../../../const/messages';
import Loader from '../../../components/Loader';
/**
 * Contact box component
 * @param props
 * @returns
 */
const ContactItem = ({ c, showMiniDetails, onClick }: {c: contact, showMiniDetails: boolean, onClick: () => void}) => {
  const shortName = useMemo(() => {
    const [f, s] = c.name.split(' ');
    let v = f.charAt(0);
    if (!s) {
      v += f.charAt(1) || '';
    } else {
      v += s.charAt(0);
    }

    return v;
  }, [c.name]);
  if (showMiniDetails) {
    return (
      <ListItem className={style.contactListItemHorizontal} onClick={onClick} data-class="contact-list-item">
        <Center className={`${style.contactListInitials} ${style.contactListInitialsHorizontal}`}>
          <Text variant="regular">{shortName}</Text>
        </Center>
        <Text width="100%" noOfLines={2} variant="regular" className={`${style.contactListName} ${style.contactListNameHorizontal} text-dark`}>{c.name}</Text>
      </ListItem>
    );
  }
  return (
    <ListItem className={style.contactListItem} onClick={onClick} data-class="contact-list-item">
      <Center className={style.contactListInitials}>
        <Text variant="regular">{shortName}</Text>
      </Center>
      <Text variant="regular" className={`${style.contactListName} text-dark`}>{c.name}</Text>
      <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
    </ListItem>
  );
};

type Props = {
  showBack: boolean;
  showSearch: boolean;
  showAdd: boolean;
  contactsPerView: number;
  pagination: boolean;
  showCompactView : boolean;
  showEmptyState?: boolean;
}

const ContactList = ({
  showBack, showSearch, showAdd, showCompactView, pagination, contactsPerView, showEmptyState
}: Props) => {
  const { t } = useTranslation();
  const page = useRef(1);
  const isSeachChanged = useRef(false);
  const navigate = useNavigate();
  const [searchKeyword, updateSearch] = useState('');

  // const {
  //   showBack, showSearch, showAdd, showCompactView, internalNavigation, pagination, contactsPerView
  // } = useModulePageData<ContactListData, Data<'contactList'>['startScreen']>('contactList');

  const account = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const { contacts, loadingState } = useAppSelector((store) => store.contact, shallowEqual);

  const dispatch = useAppAsyncDispatch();

  const goToNewContact = useCallback(() => {
    setTimeout(() => {
      navigate('/contacts/create');
    }, 0);
  }, []);

  const viewAll = useCallback(() => {

  }, []);

  const listAllContacts = useCallback((query: string | null = null) => {
    if (account) {
      const offset = (page.current - 1) * contactsPerView;
      dispatch(getAllContacts(account.id, contactsPerView, offset, query));
    }
  }, [account, page, contactsPerView]);

  useDebouncedEffect(() => {
    if (isSeachChanged.current) {
      page.current = 1;
      listAllContacts(searchKeyword);
    }
  }, 1000, [searchKeyword, isSeachChanged]);

  useEffect(() => {
    listAllContacts();
  }, [account]);

  const listUI = (isHorizontal = false) => (
    contacts.total === 0 ? (
      <Center py={2}>
        <Text variant="regular" textAlign="center" className="text-light">
          {t(contacts.total === 0 ? 'select_contact.noData' : (page.current === 1 ? '' : 'select_contact.noMoreContacts'))}
        </Text>
      </Center>
    )
      : (
        <List data-class="contact-list" border={!isHorizontal ? '1px solid var(--border-color-1)' : 'none'} className={!isHorizontal ? style.contactList : style.contactListHorizontal}>
          {contacts.data.map((c) => (
            <ContactItem
              showMiniDetails={showCompactView}
              onClick={() => {
                dispatch(updateSelectedContact(c));
                setTimeout(() => {
                  navigate(`/contacts/details/${c.id}`);
                }, 0);
              }}
              c={c}
              key={`contact-item-${c.id}`}
            />
          ))}

        </List>
      )
  );

  return (
    <Box className="page">
      <Box>
        {loadingState && !isSeachChanged.current && page.current === 1 ? (
          <Box p="15px" w="100%">
            <SkeletonLoader name={showCompactView ? 'ContactGrid' : 'ContactListDashboard'} showHeader={!showCompactView} />
          </Box>
        ) : (
          contacts.total !== 0 || showEmptyState !== false ? (
            <Box width="100%" flexDirection="column" display="flex">
              <Wrap flex="0 0 auto">
                <WrapItem width="100%">
                  <Flex align="center" gap={showBack ? '' : '25px'} width="100%" className={showBack ? `${style.contactNavContainer} navContainer` : ''}>
                    {showBack
                      ? (
                        <Icon
                          role="button"
                          aria-hidden
                          icon="Arrow-big-left"
                          className={`${style.backIcon} cancel icon-customizable`}
                          size={16}
                          data-testid="back-arrow"
                          onClick={() => {
                            sendMessage(COMMON.BACK_PRESSED, {
                              from: 'contactList'
                            });
                          }}
                        />
                      ) : null }
                    <Box className={showBack ? style.headingContainer : ''}>
                      { style.navTitle && !showCompactView
                        ? <Heading variant="navTitle" className={style.heading}>{t('select_contact.selectContacts')}</Heading>
                        : <Text variant="large">{t('select_contact.title')}</Text> }
                    </Box>
                    {showCompactView
                      ? (
                        <Text variant="regular" className="text-light" fontSize="16px" cursor="pointer" ml="auto" onClick={viewAll}>
                          {t('select_contact.viewAll')}
                          <Icon style={{ marginLeft: '8px' }} icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                        </Text>
                      ) : (showAdd && !showSearch ? (
                        <Center className={`${style.contactAddContainer} button-icon-bg`} onClick={goToNewContact}>
                          <Icon className="dark-color" icon="icon-plus" size={16} />
                          <Text className={`${style.contactAddTxt} dark-color`}>{t('select_contact.add')}</Text>
                        </Center>
                      ) : null)}
                  </Flex>
                </WrapItem>
                <WrapItem width="100%" pb="4">
                  <HStack width="100%" pt={!showCompactView ? '7px' : 0} className={showBack ? style.p15 : ''}>
                    {showSearch && !showCompactView ? (
                      <Box className={style.contactSearch}>
                        <Icon icon="icon-search" className={style.contactSearchIcon} size={15} />
                        <Input
                          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                            isSeachChanged.current = true;
                            updateSearch(e.target.value);
                          }}
                          value={searchKeyword}
                          placeholder={t('select_contact.searchContact')}
                          variant="primary"
                          className={style.contactSearchInput}
                        />
                      </Box>
                    ) : null}

                    {showAdd && showSearch && !showCompactView ? (
                      <Center className={`${style.contactAddContainer} button-icon-bg`} onClick={goToNewContact}>
                        <Icon className="dark-color" icon="icon-plus" size={16} />
                        <Text className={`${style.contactAddTxt} dark-color`}>
                          {t('select_contact.add')}
                        </Text>
                      </Center>
                    ) : null}

                  </HStack>
                </WrapItem>

              </Wrap>
              {loadingState && isSeachChanged.current ? <SkeletonLoader name="ContactListDashboard" showHeader={false} /> : (
                showCompactView || !pagination ? (
                  <div
                    className={style.scrollableDivHorizontal}
                    id="scrollableDiv"
                  >
                    {listUI(showCompactView)}
                  </div>
                )
                  : (
                    <div
                      className={`${style.scrollableDiv} ${showBack ? style.p15 : ''}`}
                      id="scrollableDiv"
                    >
                      <InfiniteScroll
                        dataLength={contacts.data.length}
                        next={() => {
                          page.current += 1;
                          listAllContacts();
                        }}
                        hasMore={contacts.data.length < contacts.total && contacts.total !== 0}
                        loader={<Center my={3}><Loader showModal={false} show /></Center>}
                        endMessage={(
              loadingState ? null
                : (
                  <Center py={2}>
                    {t(contacts.total === 0 && page.current !== 1 ? 'select_contact.noMoreContacts' : '')}
                  </Center>
                )
            )}
                        scrollableTarget="scrollableDiv"
                      >
                        {listUI()}
                      </InfiniteScroll>
                    </div>
                  )

              ) }
            </Box>
          ) : null
        )}
      </Box>
    </Box>
  );
};

export default ContactList;
